.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.grass.image::before {
    content:url('https://upload.wikimedia.org/wikipedia/commons/thumb/f/f6/Pok%C3%A9mon_Grass_Type_Icon.svg/1024px-Pok%C3%A9mon_Grass_Type_Icon.svg.png');
 }

 .pokeDex {
   padding-top: 2vh;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 10px;
 }
.pokeCard {
    position: relative;
    display: flex;
    border-style: solid; 
    border-radius: 15%;
    border-width: 2px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 24vh;
    width: 18vh;
    overflow: hidden;
     background: linear-gradient(to bottom, rgb(235, 88, 88), white 100%);
}

.pokeName {
  z-index: 1;
  position: absolute;
    align-self: center;
    top: -5px;
    font-size: 23px;
    font-weight: 500;
    text-shadow: 2300p;
    background-color: rgb(82, 175, 89);
    color: white;
    text-shadow: black 2px 2px 2px;
    height: 28px;
    border-radius: 19px 19px 19px 19px;
    margin-left: 1px;
    width: 150px;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    box-shadow: rgb(41 107 55) 2px 2px 2px 2px;
        
}
 .pokeTipos {
    position: absolute;
    bottom: 1px;
     display: flex;
     flex-direction: row;
    height: 5vh;
 }

 .typeImage {
     width: 5vh;
  }

  .pokeImage {
    position: relative;
      width: 10vh;
      
  }

  .pokemonLogoHeader {
      width: 30vh; 
  }

  .pokeBallHeader {
      width: 10vh;
  }

  .header {
      display: flex;
      justify-content: space-around;
      background: linear-gradient(to bottom, #fff782, #c4b700 100%);
      border-bottom: 3px solid rgb(123, 123, 224);
      width: 100%;      
  }

  .filterPanel {
    display: flex;
      margin-left: 5vh;
      margin-top: 5vh ;
      margin-bottom: 5vh;
      height: 13em;
      width: 70%;
  }

  .pikachuPanel {
    position: absolute;
    width: 30vh;
    padding-left: 1vh;

  }

  .switchName {
    text-shadow: rgb(78, 78, 78) 0px 1px 0px;
  }

  .botoesPanel {
    padding-left: 2em;
    width: 10%;
    height: 10%;
  }


  .mainBody {
    display: flex;
    border-style: solid;
    border-width: 10px;
    border-color: rgb(55 93 169);
    box-shadow: rgb(15, 16, 114) 4px 4px 4px 4px;
    border-radius: 20px;
    width: 120vh;
    height: 50vh;
    overflow-y: scroll;
    background: linear-gradient(to top, #fff782, #c4b700 100%);
    margin-top: 4vh;
    
  }

  .loading {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 auto;
    gap: 1em;
    padding: 0 9% 2em;
    max-width: 60%;
    max-height: 60%;
  }

  ::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888; 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555; 
  } 

  .pokemonminiaturas {
    display: flex;
  }

