    margin-left: -10%;
  }

  .miniaturaDois {
    margin-left: -20%;
  }

  .miniaturaTres {
    margin-left: -20%;
  } */


  .elementalPanel {
    flex-direction: column;
    flex-wrap: wrap;
    display: flex;
    width: 20%;
    height: 75%;
    row-gap: 4px;
    column-gap: 6px;
    padding-left: 12em;
    padding-top: 1em;
  }

  .switchTypesIMG {
    width: 90%;
    height: 90%;
    padding-top: 5px;
  }

  .painelFiltros {
   display: flex;
   flex-direction: row;
   flex-wrap: inherit;
   background: linear-gradient(to top, #fff782, #c4b700 100%);
   border-style: solid;
   border-width: 10px;
   border-color: rgb(55 93 169);
   box-shadow: rgb(15, 16, 114) 2px 2px 2px 2px;
   border-radius: 20px;
   margin-left: 40vh;
   width: 100%;
   height: 100%;
  }