.react-switch-checkbox {
  height: 0;
  width: 0;
  visibility: hidden;
}

.react-switch-label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  width: 80px;
  height: 40px;
  background: grey;
  border-radius: 100px;
  position: relative;
  transition: background-color 1s;
}

.react-switch-label .react-switch-button {
  content: '';
  position: absolute;
  top: 0px;
  left: 2px;
  width: 40px;
  height: 40px;
  border-radius: 45px;
  transition: 0.8s;
  box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.29);
}

.react-switch-button {
 background: url('https://slackmojis.com/emojis/186-pokeball/download');
 background-size: 100%;
}

.react-switch-checkbox:checked + .react-switch-label .react-switch-button {
    left: calc(100% - -1px);
    transform: translateX(-100%);
    transition: 1s;
  }

.switchName {
    padding-left: 10vh;
    white-space: nowrap;
    font-size: 23px;
    font-weight: 500;
    text-shadow: 2300p;
}